.nav__toggle {
  position: absolute;
  top: 23px;
  right: 20px;
  width: 24px;
  height: 18px;
  background: none;
  border: none;
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}
.nav--closed .nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
@media (max-width: 767px) {
  .nav--closed .nav__item {
    display: none;
  }
}
.nav--opened .nav__toggle::before,
.nav--opened .nav__toggle::after {
  content: "";
  position: absolute;
  right: 1px;
  width: 25px;
  height: 2px;
  background-color: #000000;
}
.nav--opened .nav__toggle::before {
  transform: rotate(45deg);
}
.nav--opened .nav__toggle::after {
  transform: rotate(-45deg);
}
.nav--nojs .nav__list {
  width: 100%;
  position: static;
}
@media (max-width: 767px) {
  .nav--nojs .nav__item {
    display: block;
  }
}
.nav--nojs .nav__toggle {
  display: none;
}
